.navbar {
  background-color: #001c2e;
  overflow: hidden;
  height: 100%;
  width: 100%;
}

.navbar figure {
  margin: 0;
}

.navbar img {
  cursor: pointer;
  height: 35px;
  margin: 0px 2% 0px 2%;
  padding: 5px;
  float: left;
}

#sub-body-navbar {
  margin: 0% 12% 0% 12%;
}

.container {
  width: 100%;
  display: flex;
  text-align: center;
  align-content: space-around;
}

.navbar a {
  float: left;
  color: #f2f2f2;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  flex-grow: 4;
}

.subnav {
  float: left;
  overflow: hidden;
  flex-grow: 1;
}

.subnav .subnavbtn {
  font-size: 16px;
  border: none;
  outline: none;
  width: 100%;
  color: white;
  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.navbar a:hover,
.subnav:hover .subnavbtn {
  background-color: #ddd;
  color: black;
}

#active {
  background-color: #0071b7;
  color: white;
}

#active:hover {
  color: black;
  background-color: #5fc2ff;
  transition-duration: 1s;
}

.subnav-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.subnav-content a {
  color: white;
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.subnav-content a:hover {
  background-color: #eee;
  color: black;
}

.subnav:hover .subnav-content {
  display: block;
}

/* ****************************** MOBILE DEVICES ****************************** */
@media only screen and (max-width: 768px) {
  .navbar figure {
    margin: auto;
    width: 10%;
  }

  .navbar img {
    cursor: pointer;
    height: 50px;
    float: left;
  }

  .container {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-content: space-around;
  }

  #active,
  .container a,
  .subnav {
    border-bottom: 2px solid white;
  }
}
